import Vue from 'vue';
// æ£€æµ‹æ˜¯å¦æœ‰æƒé™
// ä½¿ç”¨Vue.directiveå£°æ˜Žè‡ªå®šä¹‰æŒ‡ä»¤btn-key
export const buttonPermissions = Vue.directive('btn-key',{
    /**
     * insertedï¼šè¢«ç»‘å®šå…ƒç´ æ’å…¥çˆ¶èŠ‚ç‚¹æ—¶è°ƒç”¨ 
     * elï¼šæŒ‡ä»¤æ‰€ç»‘å®šçš„å…ƒç´ ï¼Œå¯ä»¥ç”¨æ¥ç›´æŽ¥æ“ä½œ DOM
     * binding.valueï¼šæŒ‡ä»¤çš„ç»‘å®šå€¼ï¼Œä¾‹å¦‚ï¼šv-my-directive="1 + 1" ä¸­ï¼Œç»‘å®šå€¼ä¸º 2ã€‚
     */
    inserted(el,binding){
        let buttonKey = binding.value;
        // ä»£è¡¨æŸä¸ªå…ƒç´ éœ€è¦é€šè¿‡æƒé™éªŒè¯
        if(buttonKey){
            let key = checkKey(buttonKey)
            if(!key){//æ²¡æœ‰æƒé™
                el.remove()  //åˆ é™¤æŒ‰é’®
            }
        }else{
            throw new Error('ç¼ºå°‘å”¯ä¸€æŒ‡ä»¤')
        }
    },
}) 
 
// æ£€æµ‹ä¼ å…¥çš„å…ƒç´ keyæ˜¯å¦å¯ä»¥æ˜¾ç¤º
function checkKey(key) {
    // èŽ·å–æƒé™æ•°ç»„
	// console.log(key,'è‡ªå®šä¹‰æŒ‡ä»¤');
    let permissionData = sessionStorage.getItem("permissionData") ? JSON.parse(sessionStorage.getItem("permissionData")) : [] ;
    //å¦‚æžœä¼ å…¥çš„å…ƒç´ keyä¸åœ¨æƒé™æ•°ç»„é‡Œï¼Œåˆ™ä¸å¯æ˜¾ç¤º
    let index = permissionData.indexOf(key)
    if(index > -1) {
        return true;
    }else{
        return false;
    }
}
 