
import service from "./service";
import cookies from 'vue-cookies';


//  url è¯·æ±‚è·¯å¾„
//  params å‚æ•°
//  sToken æ˜¯å¦éœ€è¦token é»˜è®¤éœ€è¦

let token = { 'X-Authorization': cookies.get('token') }

export default {
  get(url, params, isToken = true) {

    let config = {
      method: 'get',
      url: url,
    }
    if (params) {
      config.params = params;
    }
    if (isToken) {
      config = { ...config, ...token }
    }
    console.log("ðŸš€ ~ config:", config)
    return service(config);
  },
  geta(url, params, isToken = true) {

    let config = {
      method: 'get',
      url: url,
    }
    if (params) {
      config.url = `${url}/${params}`;
    }
    if (isToken) {
      config = { ...config, ...token }
    }
    return service(config);
  },
  post(url, params, isToken = true) {

    let config = {
      method: 'post',
      url: url,
    }
    if (params) {
      config.data = params;
    }
    if (isToken) {
      config = { ...config, ...token }
    }
    return service(config);
  }

};

